/* --------------------------------
 * HEADER
 * -------------------------------- */
.header {
  background: url('../img/top.jpg') no-repeat center center;
  background-size: cover;
  text-align: center;
  padding: 10px;
  color: #ffffff;
  position: relative;
  @include media(md) {
    background-position: left -63px top;
  }

  &-tit {
    font-family: $font-top;
    font-weight: normal;
    font-size: 12rem;
    line-height: 1.3;
    text-shadow: 0 2px 4px rgba(14, 39, 58, 0.3);
    @include media(md) {
      font-size: 5rem;
      line-height: 1;
      margin-bottom: 17px;
    }
  }
  &-txt {
    font-size: 4rem;
    text-shadow: 0 2px 4px rgba(14, 39, 58, 0.3);
    line-height: 1;
    margin-bottom: 117px;
    margin-top: -10px;
    @include media(md) {
      font-size: 1.8rem;
      line-height: 1.5;
      margin-bottom: 50px;
      margin-top: 0;
    }
  }
  &-btn {
    @include btn-gradient();
    padding: 23px 65px 22px 85px;
    margin-bottom: 132px;
    position: relative;
    z-index: 200;
  }

  /* &-top {
