  &-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 10px solid $main;
    z-index: 100;
    @include media(md) {
      background: rgba(255, 255, 255, 0.2);
    }
  }
}

/* gnav */
.gnav {
  display: flex;
  justify-content: space-between;
  max-width: 1217px;
  margin: 0 auto 190px;
  padding: 20px 35px 0 10px;
  @include media(md) {
    padding: 12px 9px 0 14px;
    margin-bottom: 113px;
  }

  &-logo {
    display: block;
    position: relative;
    z-index: 200;

    img {
      width: 143px;
      @include media(md) {
        width: 71px;
      }
    }
  }
  &-toggler:not(._menu) {
    display: none;
    @include media(md) {
      display: block;
      color: $main;
      font-size: 1.1rem;
      line-height: 1.090909090909091;
      font-family: $font-heading;
      font-weight: bold;
      padding: 7.5px 0;
      letter-spacing: 0.32em;
      position: relative;
      &::before {
        content: '';
        display: inline-block;
        width: 40px;
        height: 2px;
        background-color: $main;
        position: absolute;
        top: -1px;
        left: 25%;
        transform: translateX(-25%);
      }
      &::after {
        content: '';
        display: inline-block;
        width: 40px;
        height: 2px;
        background-color: $main;
        position: absolute;
        bottom: -1px;
        left: 25%;
        transform: translateX(-25%);
      }
    }
  }

  &-list {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: $main;
    z-index: 10000;
    display: none;
    @media screen and (min-width: 768px) {
      display: flex;
      position: static;
      background: transparent;
    }
  }
  &-item {
    &._sp {
      display: none;
      @include media(md) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 22px 29px 0 24px;
        margin-bottom: 36.5px;
      }

      img {
        width: 71px;
      }
      svg.svg-inline--fa {
        font-size: 2rem;
      }
    }

    a {
      display: block;
    }
  }
  &-links {
    margin-bottom: 44px;
    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      margin-bottom: 0;
    }
  }
  &-link {
    margin-right: 37px;
    @include media(md) {
      margin-right: 0;
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    a {
      font-size: 2.4rem;
      line-height: 1.125;
      font-family: $font-heading;
      color: $main;
      font-weight: bold;
      @include media(md) {
        color: #ffffff;
        line-height: 1.08333333333;
      }
    }
  }
  &-btn {
    margin-top: 2px;
    padding: 11px 33px;
    font-weight: bold;
    background: linear-gradient(#f2855e 0%, #f69b7a 48.53%, #f2855e 100%);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 180px;
    display: inline-block;
    @include media(md) {
      font-size: 1.1rem;
      line-height: 1.54545455;
      padding: 12px 30px 11px 30px;
      max-width: 150px;
      margin: 0 auto;
    }

    &:hover {
      opacity: 1;
      $start: darken(#f2855e, 5%);
      $middle: darken(#f69b7a, 5%);
      $end:  darken(#f2855e, 5%);
      background: linear-gradient($start 0%, $middle 48.53%, $end 100%);
    }
  }
}